import indexStore from "../../store";
import {useSnapshot} from "valtio";
import {Button, Card} from "antd";
import {useNavigate} from "react-router";
// @ts-ignore
import mainBg from "../../common/images/main@bg.png";
import './index.css'


const map = {
    '888830': 'SL 2',
    '762947': 'CQ 4',
    '508041': 'CK 2',
} as any

const Result = () => {
    const store = useSnapshot(indexStore)
    const navigate = useNavigate()


    return <div style={{
        display: 'flex',
        boxSizing: 'border-box',
        width: '100vw',
        height: '100vh',
        backgroundColor: "#f9f9f9",
        flexDirection: 'column',
        alignItems: 'center',
        padding: '30px 0',
        backgroundImage: `url(${mainBg})`,
        backgroundSize: 'cover',
        overflowX:"hidden"
    }}>
        <div>
            <h1 style={{ fontSize: 80, color: 'white', marginTop: 30, marginBottom: 30 }}>AI</h1>
        </div>

        <div style={{
            display: "flex",
            flexDirection: 'column',
            alignItems: "center",
            width: 1000,
            padding: 30,
            background: `linear-gradient(to left, #00FFFF, #00FFFF) left top no-repeat,
            linear-gradient(to bottom, #00FFFF, #00FFFF) left top no-repeat,
            linear-gradient(to left, #00FFFF, #00FFFF) right top no-repeat,
            linear-gradient(to bottom, #00FFFF, #00FFFF) right top no-repeat,
            linear-gradient(to left, #00FFFF, #00FFFF) left bottom no-repeat,
            linear-gradient(to bottom, #00FFFF, #00FFFF) left bottom no-repeat,
            linear-gradient(to left, #00FFFF, #00FFFF) right bottom no-repeat,
            linear-gradient(to left, #00FFFF, #00FFFF) right bottom no-repeat`,
            backgroundSize: `9px 70px, 70px 9px, 9px 70px, 70px 9px`,
        }}>
            <img style={{ width: '100%', marginBottom: 20 }} src={store.imageBase64}/>
        </div>

        <div style={{ display: "flex", alignItems: "center", margin: '30px 30px' }}>
            <div className="arrow-left"></div>
            <p style={{
                fontSize: 30,
                fontWeight: "bold",
                color: 'white',
                borderTop: '8px solid #00FFFF',
                borderBottom: '8px solid #00FFFF',
                padding: '20px 0',
            }}>{map[store.imageMd5]}</p>
            <div className="arrow-right"></div>
        </div>

        <Button
            type={'primary'} style={{ width: 300, height: 50, background: '#048EBC', flexShrink: 0, fontSize: 20 }}
            onClick={() => {
                navigate('/upload')
            }}
        >
            
        </Button>
    </div>
}

export default Result
