import { Progress } from 'antd';
import {useEffect, useState} from "react";
import { useNavigate } from "react-router";
// @ts-ignore
import mainBg from '../../common/images/main@bg.png'

const Main = () => {
    const [percent, setPercent] = useState(0)
    const navigate = useNavigate()

    useEffect(() => {
        let percent = 0

        const intervalID = setInterval(() => {
            if (percent >= 100) {
                navigate('/index')
                clearInterval(intervalID)
            } else {
                percent += 5
                setPercent(percent)
            }
        }, 430)
    }, [])

    return <div style={{
        display: 'flex',
        boxSizing: 'border-box',
        width: '100vw',
        height: '100vh',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'space-between',
        padding: '30px 0',
        backgroundImage: `url(${mainBg})`,
        backgroundSize: 'cover',
    }}>
        <h1 style={{ fontSize: 180, color: 'white', marginTop: 90 }}>AI</h1>
        <div style={{
            width: '90vw',
            padding: '10px 20px',
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center'
        }}>
            <Progress
                percent={percent}
                showInfo={false}
                strokeColor={"white"}
            />
            <p style={{ color: 'white' }}>{percent}%</p>
        </div>
    </div>
}

export default Main
