import { useState } from "react";
import { useSnapshot } from "valtio";
import { PlusOutlined } from '@ant-design/icons';
import {Button, Progress, Upload} from "antd";
import SparkMD5 from 'spark-md5';
import type { RcFile, UploadProps } from 'antd/es/upload';
import type { UploadFile } from 'antd/es/upload/interface';
import indexStore from "../../store";
import {useNavigate} from "react-router";
// @ts-ignore
import mainBg from "../../common/images/main@bg.png";


let hashMd5 = SparkMD5.ArrayBuffer.hash;


const getBase64 = (file: RcFile): Promise<string> =>
    new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => resolve(reader.result as string);
        reader.onerror = (error) => reject(error);
    });

const UploadPage = () => {
    const store = useSnapshot(indexStore)
    const navigate = useNavigate()
    const [fileList, setFileList] = useState<UploadFile[]>([]);
    const [percent, setPercent] = useState(0)
    const [showPercent, setShowPercent] = useState(false)

    const handleChange: UploadProps['onChange'] = async ({ fileList: newFileList }) => {
        const file = newFileList[0].originFileObj
        const base64 = await getBase64(newFileList[0].originFileObj as RcFile)
        const md5 = (file as any).size
        console.log(file)
        console.log(md5)
        indexStore.imageBase64 = base64
        indexStore.imageMd5 = md5
        setFileList(newFileList);
    }

    const uploadButton = (
        <div style={{
            display: "flex",
            flexDirection: 'column',
            alignItems: "center",
            width: 1000,
            padding: 30,
            background: `linear-gradient(to left, #00FFFF, #00FFFF) left top no-repeat,
                        linear-gradient(to bottom, #00FFFF, #00FFFF) left top no-repeat,
                        linear-gradient(to left, #00FFFF, #00FFFF) right top no-repeat,
                        linear-gradient(to bottom, #00FFFF, #00FFFF) right top no-repeat,
                        linear-gradient(to left, #00FFFF, #00FFFF) left bottom no-repeat,
                        linear-gradient(to bottom, #00FFFF, #00FFFF) left bottom no-repeat,
                        linear-gradient(to left, #00FFFF, #00FFFF) right bottom no-repeat,
                        linear-gradient(to left, #00FFFF, #00FFFF) right bottom no-repeat`,
            backgroundSize: `9px 70px, 70px 9px, 9px 70px, 70px 9px`,
        }}>
            <div style={{
                display: 'flex',
                boxSizing: 'border-box',
                width: '1000px',
                height: '400px',
                backgroundColor: "#048EBC",
                flexDirection: 'column',
                alignItems: 'center',
                justifyContent: "center",
                padding: '30px 0',
            }}>
                <PlusOutlined style={{color: 'white', fontSize: 30}} />
                <div style={{ marginTop: 8, fontSize: 30, color: "white" }}></div>
            </div>
        </div>
    );

    return <div style={{
        display: 'flex',
        boxSizing: 'border-box',
        width: '100vw',
        height: '100vh',
        backgroundColor: "#f9f9f9",
        flexDirection: 'column',
        alignItems: 'center',
        padding: '30px 0',
        backgroundImage: `url(${mainBg})`,
        backgroundSize: 'cover',
    }}>
        <div>
            <h1 style={{ fontSize: 80, color: 'white', marginTop: 30, marginBottom: 30 }}>AI</h1>
        </div>

        <div>

            {fileList.length === 0 ? <Upload
                action={'/123123'}
                fileList={fileList}
                onChange={handleChange}
                customRequest={() => {}}
            >
                {uploadButton}
            </Upload> : <div style={{
                display: "flex",
                flexDirection: 'column',
                alignItems: "center",
                width: 1000
            }}>
                <div style={{
                    display: "flex",
                    flexDirection: 'column',
                    alignItems: "center",
                    width: 1000,
                    padding: 30,
                    background: `linear-gradient(to left, #00FFFF, #00FFFF) left top no-repeat,
                        linear-gradient(to bottom, #00FFFF, #00FFFF) left top no-repeat,
                        linear-gradient(to left, #00FFFF, #00FFFF) right top no-repeat,
                        linear-gradient(to bottom, #00FFFF, #00FFFF) right top no-repeat,
                        linear-gradient(to left, #00FFFF, #00FFFF) left bottom no-repeat,
                        linear-gradient(to bottom, #00FFFF, #00FFFF) left bottom no-repeat,
                        linear-gradient(to left, #00FFFF, #00FFFF) right bottom no-repeat,
                        linear-gradient(to left, #00FFFF, #00FFFF) right bottom no-repeat`,
                    backgroundSize: `9px 70px, 70px 9px, 9px 70px, 70px 9px`,
                }}>

                    <img style={{ width: '100%', marginBottom: 20 }} src={store.imageBase64}/>
                </div>
                <div style={{ backgroundColor: 'white', borderRadius: 6 }}>
                    <Button
                        type={'primary'}
                        style={{ width: 300, height: 50, background: '#048EBC', fontSize: 20 }} onClick={() => { setFileList([]) }}
                    ></Button>
                </div>
            </div>}
        </div>

        <div style={{
            marginTop: 50,
            display: 'flex',
            boxSizing: 'border-box',
            flexDirection: 'column',
            alignItems: 'center',

        }}>
            {showPercent && <Progress
                percent={percent}
                showInfo={false}
                strokeColor={"#048EBC"}
            />}
            <div style={{ backgroundColor: 'white', borderRadius: 6 }}>
                <Button
                    type={'primary'} style={{ width: 300, height: 50, background: '#048EBC', fontSize: 20 }}
                    onClick={() => {
                        setShowPercent(true)

                        let percent = 0

                        const intervalID = setInterval(() => {
                            if (percent >= 100) {
                                navigate('/result')
                                clearInterval(intervalID)
                            } else {
                                percent += 5
                                setPercent(percent)
                            }
                        }, 300)
                    }}
                >
                    
                </Button>
            </div>

        </div>

    </div>
}

export default UploadPage