import Main from "../pages/Main";
import Index from "../pages/Index";
import Upload from "../pages/Upload";
import Result from "../pages/Result";

const routes = [
    {
        path: '/',
        element: <Main />
    },
    {
        path: '/main',
        element: <Main />
    },
    {
        path: '/index',
        element: <Index />
    },
    {
        path: '/upload',
        element: <Upload />
    },
    {
        path: '/result',
        element: <Result />
    }
]

export default routes
